.news-inner {
  padding: 116px 0 0;
}

.propose {
  display: flex;
  justify-content: space-between;
  margin-bottom: 80px;
  overflow: hidden;
  border-radius: 24px;
  background: #fff;

  &__img {
    width: 100%;
    max-width: 472px;

    img {
      height: 100%;
      min-height: 472px;
      border-radius: 24px 0 0 24px;
      object-fit: cover;
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0 auto;
    padding: 0 24px;
    width: 100%;
    max-width: 548px;

    a {
      margin-bottom: 14px;
      font-weight: 700;
      font-size: 32px;
      line-height: 40px;
      letter-spacing: -0.02em;

      span {
        width: calc(100%);
        background-image: linear-gradient(transparent calc(100% - 2px), $black 2px);
        background-size: 0 90%;
        background-repeat: no-repeat;
        transition: background-size 0.25s cubic-bezier(0.785, 0.135, 0.15, 0.86) 0s;
      }

      &:hover {
        span {
          background-size: 100% 90%;
        }
      }
    }

    h4 {
      margin-bottom: 5px;
      font-weight: 700;
      font-size: 12px;
      line-height: 16px;
      color: $green;
    }

    p {
      margin-bottom: 24px;
      font-size: 16px;
      line-height: 24px;
      letter-spacing: -0.01em;
      color: $grey-text;
    }
  }

  .date {
    max-width: 500px;
  }
}

.story {
  display: flex;
  justify-content: space-between;

  &__sidebar {
    width: 100%;
    max-width: 240px;

    li {
      font-size: 0;
      letter-spacing: -0.01em;

      &:not(:last-child) {
        margin-bottom: 14px;
      }
    }

    p {
      font-size: 14px;
      line-height: 20px;
      color: $grey-text;
    }

    span {
      font-weight: 700;
      font-size: 18px;
      line-height: 28px;
    }

    svg {
      display: inline-block;
      transition: all 0.2s;
      cursor: pointer;

      &:not(:last-child) {
        margin-right: 6px;
      }

      &:hover {
        fill: #0f0f11;

        path {
          fill: #f5f5f9;
        }
      }
    }

    .link-icon {
      transition: all 0.2s;
      cursor: pointer;

      &:hover {
        fill: #0f0f11;

        path {
          fill: transparent;
          stroke: #f5f5f9;
        }
      }
    }
  }

  &__wrapper {
    width: 100%;
    max-width: 912px;
  }

  &__inner {
    margin: 0 auto;
    max-width: 680px;

    p:not(:last-child) {
      margin-bottom: 32px;
    }

    h3 {
      margin-bottom: 25px;
      font-weight: 700;
      font-size: 32px;
      line-height: 40px;
      letter-spacing: -0.02em;
    }

    h4 {
      margin: 62px 0 18px;
      font-weight: 700;
      font-size: 24px;
      letter-spacing: -0.02em;
    }

    h5 {
      margin: 0 0 9px;
      font-weight: 700;
      font-size: 20px;
      letter-spacing: -0.01em;
    }

    ul {
      li {
        position: relative;
        padding-left: 18px;

        &::after {
          content: "";
          position: absolute;
          left: 0;
          top: 13px;
          width: 6px;
          height: 6px;
          border-radius: 50%;
          background-color: $green;
        }

        &:not(:last-child) {
          margin-bottom: 24px;
        }
      }
    }

    ol {
      li {
        position: relative;
        padding-left: 28px;
        list-style: none;

        &::after {
          content: "" counter(section) ".";
          position: absolute;
          left: 0;
          top: 0;
          width: 26px;
          height: 32px;
          counter-increment: section;
          font-weight: 700;
          font-size: 18px;
          line-height: 32px;
          letter-spacing: -0.01em;
          text-align: center;
        }

        &:not(:last-child) {
          margin-bottom: 24px;
        }
      }
    }
  }

  &__img {
    position: relative;
    margin: 55px 0 45px;
    width: 100%;

    img {
      margin-bottom: 12px;
      width: 100%;
      border: 24px;
      object-fit: cover;
    }

    span {
      position: absolute;
      left: 16px;
      bottom: 40px;
      font-size: 12px;
      line-height: 16px;
      color: $white;
    }

    p {
      font-weight: 300;
      font-style: italic;
      font-size: 12px;
      line-height: 16px;
      text-align: center;
    }
  }

  &__notice {
    margin-bottom: 48px;
    padding: 20px 20px 20px 24px;
    border-radius: 12px;
    color: $white;
    background-color: $black;
  }

  &__quote {
    margin: 79px auto 76px;
    padding-left: 20px;
    max-width: 672px;
    border-left: 2px solid $green;

    h4 {
      margin-bottom: 11px;
      font-weight: 700;
      font-size: 32px;
      line-height: 40px;
      letter-spacing: -0.02em;
    }

    p {
      font-weight: 700;
      font-size: 12px;
      line-height: 16px;
    }
  }
}

.next {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 239px;
  min-height: 384px;
  border-radius: 0 0 24px 24px;
  background-color: $white;

  .container {
    display: flex;
    width: 100%;
  }

  img {
    margin-top: -115px;
    max-width: 368px;
    border-radius: 24px;
    object-fit: cover;
  }
}
